.project-header
{
    gap: 2rem;
    color: #fff;
    display: flex;
    font-size: 3rem;
    font-style: italic;
    font-weight: 700;
    gap: 5rem;
    justify-content: center;
    text-transform: uppercase;
}
.plans-container
{
    margin-top: -4rem;
    padding: 0 2rem;
    display: flex;
    flex-direction: column;
    gap: 4rem;
    position: relative;
}
.plans
{
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 3rem;
}
.plan
{
    display: flex;
    flex-direction: column;
    /* background:var(--caloryCard); */
    background: var(--planCard);
    color: white;
    gap:2rem;
    padding: 1.5rem;
    width: 15rem;
}
.plan:nth-child(2)
{
    background: var(--planCard);
    transform: scale(1.1);

}
.plan>svg
{
    fill: rgb(223, 191, 170);
    width: 2rem;
    height: 2rem;
}
.plan>:nth-child(2)
{
    font-size: 1rem;
    font-weight: bold;
}
.plan>:nth-child(3)
{
    font-size: 3rem;
    font-weight: bold;
}
.plan>:nth-child(5)
{
    font-size: 0.8rem;
    
}
.features
{
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.feature
{
   display: flex;
   align-items: center;
   gap: 1rem;
}
.feature>img
{
    width: 1rem;
}
.plans-blur-1
{
   width: 32rem;
   height: 23rem;
   top: 6rem;
   left: 0rem;
}
.plans-blur-2
{
   width: 32rem;
   height: 23rem;
   top: 10rem;
   right: 0rem;
}
@media screen and (max-width: 768px)
{
    .plans {
        flex-direction: column;
    }
    .plans>:nth-child(2) {
        -webkit-transform: none;
        transform: none;
    }
}