.Testimonials
{
    display: flex;
    gap: 1rem;
    padding: 0 2rem;
}
.left-t
{
    display: flex;
    flex: 1 1;
    gap: 2rem;
    flex-direction: column;
    color: white;
}
.left-t>:nth-child(1)
{
    color: var(--orange);
    font-weight: bold;

}
.left-t>:nth-child(2),
.left-t>:nth-child(3)
{
    font-weight: bold;
    font-size: 3rem;
}
.left-t>:nth-child(4)
{
    text-transform: none;
    text-align: justify;
    letter-spacing: 2px;
    line-height: 40px;
}
.right-t
{
    flex: 1;
    position: relative;
}
.right-t>img {
    height: 20rem;
    position: absolute;
    width: 17rem;
    object-fit: cover;
    right: 8rem;
    top: 2rem;
}
.right-t>:nth-child(1)
{
    position: absolute;
    width: 17rem;
    height: 20rem;
    border: 2px solid orange;
    background-color: transparent;
    right: 9rem;
    top: 0.9rem;
}
.right-t>:nth-child(2)
{
    position: absolute;
    width: 17rem;
    height: 19rem;
    right: 7rem;
    top:4rem;
    background: var(--planCard);
}
.arrows
{
    display: flex;
    gap: 1rem;
    position: absolute;
    bottom: 1rem;
    left: 3rem;

}
.arrows>img
{
    width: 1.5rem;
    cursor: pointer;
}
@media screen and (max-width: 767px)
{
.testimonials {
    flex-direction: column;
}
.right-t {
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 2rem;
    justify-content: center;
}
.right-t>img {
    align-self: center;
    position: relative;
    right: 0;
    top: 0;
}
.right-t>:last-child {
    bottom: 0;
    display: block;
    left: 0;
}
}